import React from 'react';
import { Link } from 'react-router-dom';

import './header.css';

const Header = ({ onServiceChange }) => {
  return (
    <div className="header d-flex">
      <h3>
        <Link to='/star-wars-db/'>
          Star DB
        </Link>
      </h3>
      <ul className="d-flex">
        <li>
          <Link to='/star-wars-db/people/'>People</Link>
        </li>
        <li>
        <Link to='/star-wars-db/planets/'>Planets</Link>
        </li>
        <li>
        <Link to='/star-wars-db/starships/'>Starships</Link>
        </li>
        <li>
        <Link to='/star-wars-db/login'>Login</Link>
        </li>
        <li>
        <Link to='/star-wars-db/secret'>Secret</Link>
        </li>
      </ul>
      <button
        onClick={onServiceChange}
        className='btn btn-primary btn-sm'>
        Change services
      </button>
    </div>
  );
};

export default Header;